<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="100px">
      <el-form-item label="参数代码" prop="parmCode">
        <el-input v-model="dataForm.parmCode"></el-input>
      </el-form-item>
      <el-form-item label="参数名称" prop="parmName">
        <el-input v-model="dataForm.parmName"></el-input>
      </el-form-item>
      <el-form-item label="参数值" prop="parmValue">
        <el-input type="textarea" :rows="5" v-model="dataForm.parmValue"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        dataForm: {
          parmId: 0,
          parmCode: '',
          parmName: '',
          parmValue: ''
        },
        dataRule: {
          parmCode: [
            { required: true, message: '参数代码必填', trigger: 'blur' }
          ],
          parmName: [
            { required: true, message: '参数名称必填', trigger: 'blur' }
          ],
          parmValue: [
            { required: true, message: '参数值必填', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      init (row) {
        this.dataForm.parmId=row && row.parmId || 0
        this.visible=true
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if(this.dataForm.parmId) {
            this.dataForm.parmCode=row.parmCode
            this.dataForm.parmName=row.parmName
            this.dataForm.parmValue=row.parmValue
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.dataForm.parmId=this.dataForm.parmId || undefined
            this.$http({
              url: this.$http.adornUrl(this.API[!this.dataForm.parmId?'addQueryParam':'updateQueryParam']),
              method:'post',
              data:this.$http.adornData(this.dataForm)
            }).then(({data}) => {
              if(data && data.code===0){
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              }else{
                this.$message.error(data.msg);
              }
            })
          }
        })
      }
    }
  }
</script>



