<template>
  <!-- 系统参数管理 -->
  <div>
    <el-form class="float-right" :inline="true">
      <el-form-item>
        <el-button type="primary" size="small" @click="addOrUpdateHandle()">添加</el-button>
        <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
        <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
        <el-button type="primary" size="small" @click="sortHanlde">保存排序</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="dataList"
      border
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      :max-height="siteContentViewHeight - 66 +'px'"
      style="width: 100%;">
      <el-table-column type="selection" width="40"></el-table-column>
      <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
      <el-table-column prop="parmCode" width="300" label="参数代码"></el-table-column>
      <el-table-column prop="parmName" width="200" label="参数名称"></el-table-column>
      <el-table-column prop="parmValue" label="参数值"></el-table-column>
      <el-table-column width="150" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" plain @click="addOrUpdateHandle(scope.row)">修改</el-button>
          <el-button type="danger" size="mini" plain @click="deleteHandle(scope.$index,scope.row.parmId)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 弹窗, 新增 / 修改 -->
    <add-or-update v-if="addOrUpdateVisible" ref="addOrUpdate" @refreshDataList="getDataList"></add-or-update>
  </div>
</template>

<script>
  import AddOrUpdate from './add-or-update'
  import {swapItems} from '@/utils';
  export default{
    props:['siteContentViewHeight'],
    data(){
      return{
        dataList:[],
        dataListLoading:false,
        dataListSelections:[],
        addOrUpdateVisible: false
      }
    },
    components:{AddOrUpdate},
    created () {
      this.getDataList()
    },
    methods:{
      getDataList () {// 获取数据列表
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl(this.API.getQueryParam),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList=data.data
          }else{
            this.$message.error(data.msg);
            this.dataList=[]
          }
          this.dataListLoading = false
        });
      },
      addOrUpdateHandle(row){// 新增 / 修改
        this.addOrUpdateVisible = true
        this.$nextTick(() => {
          this.$refs.addOrUpdate.init(row)
        })
      },
      selectionChangeHandle(val) {//多选
        this.dataListSelections=val;
      },
      moveDirection(direction){ //移动
      	var that=this,index;
      	that.dataListSelections.sort((prev,next)=>{
      		return that.dataList.indexOf(prev)-that.dataList.indexOf(next)
      	})
      	if(direction=='down') that.dataListSelections.reverse()
      	that.dataListSelections.forEach(item=>{
      		index=that.dataList.indexOf(item); //得到右侧选中值的标识坐标
      		if(direction=='up') that.moveUp(index);
      		else if(direction=='down') that.moveDown(index)
      	})
      },
      moveUp(index){//上移
      	if(index==0) return;
      	this.dataList=swapItems(this.dataList,index,index-1,'up');
      },
      moveDown(index){ //下移
      	if(index==this.dataList.length-1) return;
      	this.dataList=swapItems(this.dataList,index,index+1,'down');
      },
      sortHanlde(){//保存排序
        var sortArr=[];
        if(this.dataList.length<0) return;
        this.dataList.forEach((item,index)=>{
          sortArr.push({
            parmId:item.parmId,
            orderno:index+1
          })
        })
        this.$http({
          url: this.$http.adornUrl(this.API.sortQueryParam),
          method: 'post',
          data: this.$http.adornData({
            data:JSON.stringify(sortArr)
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      deleteHandle(index,id){
        this.$confirm('确定要删除该信息？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl(this.API.deleteQueryParam),
            method: 'post',
            data: this.$http.adornData({
              parmId:id
            })
          }).then(({data}) => {
            if(data && data.code===0){
              this.dataList.splice(index,1);
              this.$message.success('操作成功');
            }else{
              this.$message.error(data.msg);
            }
          });
        }).catch(() => {})
      }
    }
  }
</script>

<style scoped>
  .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 10px;}
  .el-button.is-circle{}
</style>



